.container {
    height: calc(100vh - 75px);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 5rem;
    padding-bottom: 5rem;
  }
  
  .wrapper {
    display: flex;
    flex-direction: column;
    border: 1px solid #444;
    border-radius: 22px;
    padding: 1.5rem;
  }
  
  .title {
    text-align: center;
    margin-bottom: 1.5rem;
    font-size: 26px;
    font-weight: bold;
  }
  
  .inputWrapper {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-bottom: 0.5rem;
  }
  
  .inputWrapper>label {
    margin-right: 15px;
    width: 33%;
  }
  
  .inputWrapperImgFirst {
    width: 400px;
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
  }
  
  .inputWrapperImgFirst span {
    display: inline-block;
    padding: 8px 16px;
    background-color: teal;
    color: #fff;
    border: 1px solid transparent;
    cursor: pointer;
    transition: 150ms all;
    border-radius: 16px;
    margin: 0.1rem 2.5rem;
  }
  
  .inputWrapperImgFirst span:hover {
    padding: 8px 16px;
    background-color: #fff;
    color: teal;
    border-color: teal;
  }
  
  .inputWrapperImgSecond {
    width: 400px;
    display: flex;
    align-items: center;
    margin-bottom: 0.5rem;
  }
  
  .inputWrapperImgSecond span {
    display: inline-block;
    padding: 8px 16px;
    background-color: teal;
    color: #fff;
    border: 1px solid transparent;
    cursor: pointer;
    transition: 150ms all;
    border-radius: 16px;
    margin: 0.1rem 2.5rem;
    margin-left: 1rem;
  }
  
  .inputWrapperImgSecond span:hover {
    padding: 8px 16px;
    background-color: #fff;
    color: teal;
    border-color: teal;
  }
  
  .imageName {
    position: absolute;
    right: 35rem;
    display: flex;
    align-items: center;
    gap: 6px;
    font-size: 14px;
  }
  
  .closeIcon {
    cursor: pointer;
    font-size: 16px;
  }
  
  .input {
    padding: 0.5rem;
    width: 77.5%;
    border-radius: 8px;
    border: 1px solid rgb(170, 165, 165);
  }
  
  .input:focus {
    outline: none;
    border-color: rgb(99, 92, 92);
  }
  
  .buttonWrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 2px;
  }
  
  .submitBtn {
    width: 50%;
    background-color: #cea600;
    color: #fff;
    padding: 0.75rem 1.25rem;
    border-radius: 12px;
    border: 1px solid transparent;
    cursor: pointer;
    transition: 150ms all;
    margin-top: 1rem;
  }
  
  .submitBtn:hover {
    background-color: #fff;
    color: #cea600;
    border-color: #cea600;
  }
  
  .successMessage {
    height: 125px;
    width: 300px;
    text-align: center;
    border-radius: 20px;
    line-height: 1.5rem;
    padding: 3rem 5rem;
    background-color: green;
    color: #fff;
    position: fixed;
    font-size: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 5rem;
    right: 5rem;
  }